import React from 'react'
import { Form, Radio } from 'semantic-ui-react'

import andrew from './resources/Andrew.png'
import maryam from './resources/Maryam.png'

const images = {
    andrew,
    maryam
}
class Question extends React.Component {
    constructor () {
        super()
        this.state = {imageName: null, options: [], value: undefined}
    }

    pickThree = () => {
        let three = []
        const max = this.props.members.length
        let i = 0
        // generate three other random options for the multi-choice
        while (i < 3) {
            let index = Math.floor(Math.random() * max)
            if (this.props.members[index] !== this.state.imageName && !three.includes(this.props.members[index])) {
                i++
                three.push(this.props.members[index])
            }
        }
        return three
    }

    shuffle = (options) => {
        const numberOfChoices = 4
        let index = Math.floor(Math.random() * numberOfChoices)
        if (index !== 0) {
            const temp = options[0]
            options[0] = options[index]
            options[index] = temp
        }
        console.log('options', options)
    }

    componentDidMount () {
        const imageName = `${this.props.members[this.props.questionNumber - 1]}`
        console.log('didmount', imageName, this.state.image)
        this.setState({imageName: imageName}, this.constructChoices)
        // this.constructChoices(imageName)
    }

    didComponentUpdate (prevProps) {
        console.log('didUpdate')
        if (prevProps.questionNumber !== this.props.questionNumber) {
            console.log('didUpdate2')
            const imageName = `${this.props.members[this.props.questionNumber - 1]}`
            // this.setState({imageName})
            this.constructChoices(imageName)
        }
    }

    constructChoices = () => {
        let options = []
        console.log('construct state us', this.state)
        options.push(this.state.imageName)
        console.log('construct options is', options)
        const three = this.pickThree(this.state.imageName)
        console.log('construct three is', three)
        three.forEach(option => options.push(option))
        console.log('constructchoices options', options)
        this.shuffle(options)
        this.setState({options, value: undefined})
    }

    renderChoices = () => {
        console.log('renderChoices options is', this.state.options)
        return (
            <Form>
            <Form.Field>
              Selected value: <b>{this.state.value}</b>
            </Form.Field>
            {this.state.options.map(option => {
                return <Form.Field key={option}>
                <Radio
                  label={option}
                  name='radioGroup'
                  value={option}
                  checked={this.state.value === option}
                  onChange={this.handleChange}
                />
              </Form.Field>
            })}
          </Form>
        )
    }
    handleChange = (e, { value }) => this.setState({ value })

    render () {
        const imageName = `${this.props.members[this.props.questionNumber - 1]}`
        const fullImageName = `${imageName}.png`
        console.log('image name', `./resources/${imageName}`)
        console.log('images', images)
        return (
            <div>
                <div>Whose git history is this?</div>
                <div> <img src={images[imageName]} width='500px' height='200px' /></div>
                {this.renderChoices(imageName)}
            </div>
        )

    } 

}

export default Question
